"use client";

import { useState, useRef, useEffect } from "react";
import { createClient } from "@supabase/supabase-js";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "../common/Card";
import { Input } from "../common/Input";
import Button from "../common/Buttons";
import { ScrollArea } from "../common/ScrollArea";
import { Send, Bot, User, X, Trash2 } from "lucide-react";

interface Message {
  id: string;
  role: "user" | "assistant";
  content: string;
  timestamp: string;
}

export function TodoChatbot({ onClose }: { onClose: () => void }) {
  const [messages, setMessages] = useState<Message[]>([
    { 
      id: "1", 
      role: "assistant", 
      content: "Hi! I'm your todo assistant. Ask me about your tasks, completion rates, or when you completed specific tasks. For example: 'How many todos did I complete this week?' or 'When did I complete my project report?'", 
      timestamp: new Date().toISOString() 
    }
  ]);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [userId, setUserId] = useState<string | null>(null);
  const [authError, setAuthError] = useState<string | null>(null);
  const scrollRef = useRef<HTMLDivElement>(null);

  // Get authenticated user on component mount
  useEffect(() => {
    const getUser = async () => {
      try {
        const supabase = createClient(
          process.env.NEXT_PUBLIC_SUPABASE_URL!,
          process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
        );

        const { data, error } = await supabase.auth.getUser();

        if (error) {
          console.error("Auth error:", error);
          setAuthError("Failed to get user authentication");
          return;
        }

        if (data.user) {
          console.log("User authenticated:", data.user.id);
          setUserId(data.user.id);
          // Load chat history from localStorage
          loadChatHistory(data.user.id);
        } else {
          setAuthError("No user found. Please log in.");
        }
      } catch (err) {
        console.error("Error getting user:", err);
        setAuthError("Error retrieving authentication");
      }
    };

    getUser();
  }, []);

  // Auto scroll to bottom when new messages arrive
  useEffect(() => {
    if (scrollRef.current) {
      setTimeout(() => {
        if (scrollRef.current) {
          scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
        }
      }, 0);
    }
  }, [messages]);

  // Load chat history from localStorage
  const loadChatHistory = (uid: string) => {
    try {
      const saved = localStorage.getItem(`chatbot_history_${uid}`);
      if (saved) {
        const history = JSON.parse(saved);
        if (Array.isArray(history) && history.length > 0) {
          setMessages(history);
        }
      }
    } catch (err) {
      console.error("Error loading chat history:", err);
    }
  };

  // Save chat history to localStorage
  const saveChatHistory = (msgs: Message[]) => {
    if (userId) {
      try {
        localStorage.setItem(`chatbot_history_${userId}`, JSON.stringify(msgs));
      } catch (err) {
        console.error("Error saving chat history:", err);
      }
    }
  };

  // Clear chat history
  const clearChatHistory = () => {
    if (userId && window.confirm("Are you sure you want to clear the chat history?")) {
      setMessages([
        { 
          id: "1", 
          role: "assistant", 
          content: "Hi! I'm your todo assistant. Chat history cleared. Ask me about your tasks!", 
          timestamp: new Date().toISOString() 
        }
      ]);
      try {
        localStorage.removeItem(`chatbot_history_${userId}`);
      } catch (err) {
        console.error("Error clearing chat history:", err);
      }
    }
  };

  const handleSend = async () => {
    if (!input.trim() || isLoading) return;

    if (!userId) {
      setMessages(prev => [...prev, { 
        id: Date.now().toString(), 
        role: "assistant", 
        content: authError || "Error: Not authenticated. Please refresh the page and log in.", 
        timestamp: new Date().toISOString() 
      }]);
      return;
    }

    const userMessage: Message = { 
      id: Date.now().toString(), 
      role: "user", 
      content: input, 
      timestamp: new Date().toISOString() 
    };
    
    const updatedMessages = [...messages, userMessage];
    setMessages(updatedMessages);
    saveChatHistory(updatedMessages);
    setInput("");
    setIsLoading(true);

    try {
      const FUNCTION_URL = "/api/chatbot";
      console.log("Sending to:", FUNCTION_URL);
      console.log("User ID:", userId);
      console.log("Query:", userMessage.content);

      const response = await fetch(FUNCTION_URL, {
        method: "POST",
        headers: { 
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          query: userMessage.content,
          user_id: userId,
        }),
      });

      console.log("Response status:", response.status);

      if (!response.ok) {
        let errorMsg = "Failed to get response";
        try {
          const errData = await response.json();
          errorMsg = errData.error || JSON.stringify(errData);
        } catch {
          const text = await response.text();
          errorMsg = `HTTP ${response.status}: ${text}`;
        }
        console.error("API Error:", errorMsg);
        throw new Error(errorMsg);
      }

      const data = await response.json();
      console.log("AI Response:", data.reply?.substring(0, 50) + "...");

      const assistantMessage: Message = { 
        id: (Date.now() + 1).toString(), 
        role: "assistant", 
        content: data.reply, 
        timestamp: new Date().toISOString() 
      };
      
      const finalMessages = [...updatedMessages, assistantMessage];
      setMessages(finalMessages);
      saveChatHistory(finalMessages);

    } catch (err) {
      const errorMsg = err instanceof Error ? err.message : "Something went wrong.";
      console.error("Chat error:", errorMsg);
      const errorMessage: Message = { 
        id: (Date.now() + 1).toString(), 
        role: "assistant", 
        content: `Error: ${errorMsg}`, 
        timestamp: new Date().toISOString() 
      };
      const finalMessages = [...updatedMessages, errorMessage];
      setMessages(finalMessages);
      saveChatHistory(finalMessages);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Card className="h-full flex flex-col rounded-lg border-0">
      <CardHeader className="border-b flex justify-between items-center p-4">
        <div className="flex-1">
          <CardTitle className="flex items-center gap-2 text-lg">
            <Bot className="h-5 w-5 text-primary" /> 
            Todo Assistant
          </CardTitle>
          <CardDescription className="text-xs mt-1">
            {userId ? "Ask me anything about your tasks" : "Loading..."}
          </CardDescription>
        </div>
        <div className="flex gap-2">
          <Button 
            variant="primary" 
            size="sm" 
            onClick={clearChatHistory}
            title="Clear chat history"
          >
            <Trash2 className="h-4 w-4" />
          </Button>
          <Button variant="primary" size="sm" onClick={onClose}>
            <X className="h-4 w-4" />
          </Button>
        </div>
      </CardHeader>

      <CardContent className="flex-1 flex flex-col p-0 overflow-hidden">
        <ScrollArea className="flex-1 p-4 overflow-y-auto" ref={scrollRef}>
          <div className="space-y-4 pr-4">
            {messages.map(msg => (
              <div key={msg.id} className={`flex gap-3 ${msg.role === "user" ? "justify-end" : "justify-start"}`}>
                {msg.role === "assistant" && (
                  <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center flex-shrink-0">
                    <Bot className="h-4 w-4 text-primary-foreground" />
                  </div>
                )}
                <div className={`rounded-lg px-4 py-2 max-w-xs break-words ${msg.role === "user" ? "bg-primary text-primary-foreground" : "bg-muted text-foreground border border-border"}`}>
                  <p className="text-sm whitespace-pre-wrap">{msg.content}</p>
                  <span className="text-xs opacity-70 mt-1 block">
                    {new Date(msg.timestamp).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' })}
                  </span>
                </div>
                {msg.role === "user" && (
                  <div className="h-8 w-8 rounded-full bg-accent flex items-center justify-center flex-shrink-0">
                    <User className="h-4 w-4 text-accent-foreground" />
                  </div>
                )}
              </div>
            ))}
            {isLoading && (
              <div className="flex gap-3 justify-start">
                <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center flex-shrink-0">
                  <Bot className="h-4 w-4 text-primary-foreground" />
                </div>
                <div className="rounded-lg px-4 py-2 bg-muted text-foreground border border-border">
                  <p className="text-sm animate-pulse">Thinking...</p>
                </div>
              </div>
            )}
          </div>
        </ScrollArea>

        <div className="border-t p-4 flex gap-2 bg-background">
          <Input 
            value={input} 
            onChange={e => setInput(e.target.value)} 
            onKeyDown={e => e.key === "Enter" && !e.shiftKey && handleSend()} 
            placeholder="Ask about your todos..." 
            disabled={isLoading || !userId}
            className="flex-1"
          />
          <Button 
            onClick={handleSend} 
            disabled={isLoading || !userId}
            size="sm"
          >
            <Send className="h-4 w-4" />
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}