"use client";

import React, { useState, useEffect } from "react";
import { supabase } from "../../lib/supabase-client"; 
import { Dialog } from "@headlessui/react";
import { Task } from "../../lib/type"; 

// Props 
interface DialogNotesProps {
  task: Task | null;
  isOpen: boolean;
  onClose: () => void;
  setTasks: React.Dispatch<React.SetStateAction<Task[]>>;
}

const DialogNotes: React.FC<DialogNotesProps> = ({ task, isOpen, onClose, setTasks }) => {
  const [notes, setNotes] = useState<string>(task?.notes || "");

  // Update notes when task changes
  useEffect(() => {
    if (task) {
      setNotes(task.notes || "");
    }
  }, [task]);

  const handleSaveNotes = async () => {
    if (!task) return;

    const updatedNotes = notes;

    
    const { error } = await supabase
      .from("todos")
      .update({ notes: updatedNotes })
      .eq("id", task.id);

    if (error) {
      console.error("Error saving notes:", error.message);
      return;
    }

    // Update local state for UI immediately
    setTasks(prevTasks =>
      prevTasks.map(t =>
        t.id === task.id ? { ...t, notes: updatedNotes } : t
      )
    );

    onClose(); // Close the dialog after saving
  };

  return (
    <Dialog open={isOpen} onClose={onClose}>
      <div className="fixed inset-0 flex items-center justify-center z-50">
        <Dialog.Panel className="bg-white p-6 rounded-lg max-w-lg w-full">
          <Dialog.Title className="text-xl font-semibold">Notes for Task</Dialog.Title>
          <Dialog.Description className="text-sm mt-2 mb-4">{task?.title}</Dialog.Description>

          <div className="space-y-4">
            <textarea
              placeholder="Enter your notes here..."
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-md"
              rows={6}
            />

            <div className="flex justify-end gap-2">
              <button
                onClick={onClose}
                className="px-4 py-2 bg-gray-300 text-gray-700 rounded-md"
              >
                Cancel
              </button>
              <button
                onClick={handleSaveNotes}
                className="px-4 py-2 bg-blue-500 text-white rounded-md"
              >
                Save Notes
              </button>
            </div>
          </div>
        </Dialog.Panel>
      </div>
    </Dialog>
  );
};

export default DialogNotes;
